<template>
  <div class="chatbot-message end-message">
    <div class="message-author">
      chatbot
    </div>
    <div class="message-content">
      <div>
        Did you find this conversation helpful ?
      </div>

      <div>
        <a
          href="#!"
          class="modal-link-click-exception"
          @click="sendAnswer(true)"
          >Yes</a
        >
        <a
          href="#!"
          class="modal-link-click-exception"
          @click="sendAnswer(false)"
          >No</a
        >
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "ChatBotEndMessage",

  methods: {
    sendAnswer(status) {
      this.$emit("helpful", status);
    }
  }
};
</script>

