<template>
  <div class="row">
    <div class="input-field col s12">
      <input
        v-model="userTextMessage"
        type="text"
        @keydown.enter="sendMessage"
      />
    </div>
  </div>
</template>
<script>
export default {
  name: "UserInput",

  data: function() {
    return {
      userTextMessage: ""
    };
  },
  methods: {
    sendMessage() {
      if (this.userTextMessage !== "") {
        this.$emit("message", this.userTextMessage);
        this.userTextMessage = "";
      }
    }
  }
};
</script>

