<template>
  <div>
    <div
      v-show="!endConversation"
      id="chatbot-app-and-header-wrapper"
      class="z-depth-2"
    >
      <div>
        <h4 class="">Talk with our chatbot</h4>
      </div>
      <div id="chat-container">
        <div id="message-container" ref="messageContainerScroll">
          <div v-for="(message, index) in messages" :key="index">
            <chat-bot-message
              v-if="message.author == 'chatbot'"
              :content="message.content"
            ></chat-bot-message>
            <user-message v-else :content="message.content"></user-message>
          </div>
          <div>
            <chat-bot-spinner
              v-if="isWaitingChatbotResponse"
            ></chat-bot-spinner>
            <chat-bot-end-message
              v-if="showWasHelpful"
              @helpful="sendHelpfulStatus"
            ></chat-bot-end-message>
          </div>
        </div>
        <div class="input-container">
          <user-input v-on:message="sendMessage"></user-input>
        </div>
      </div>
    </div>

    <div v-show="endConversation">
      <div class="row">
        <div class="col s12">
          <div class="card teal darken-1">
            <div class="card-content white-text">
              <span class="card-title">Thank you !</span>
              <p>
                Thank you for your feedback ! You can find this conversation
                inside your history
              </p>
            </div>
            <div class="card-action" @click="startConversation">
              <a href="#!" class="modal-link-click-exception"
                >Start a new conversation</a
              >
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UserInput from "./components/UserInput.vue";
import UserMessage from "./components/UserMessage.vue";
import ChatBotMessage from "./components/ChatBotMessage.vue";
import ChatBotSpinner from "./components/ChatBotSpinner.vue";
import ChatBotEndMessage from "./components/ChatBotEndMessage.vue";
import axios from "axios";

export default {
  name: "ChatBot",
  components: {
    UserInput,
    UserMessage,
    ChatBotMessage,
    ChatBotSpinner,
    ChatBotEndMessage
  },
  data: function() {
    return {
      isWaitingChatbotResponse: false,
      requestsCounter: 0,
      messages: [],
      showWasHelpful: false,
      endConversation: false
    };
  },

  mounted() {
    this.sendStartConversationRequest();
  },

  methods: {
    sendMessage(userTextMessage) {
      if (this.showWasHelpful) {
        this.showWasHelpful = false;
        this.messages.push({
          author: "chatbot",
          content: "As you want let's continue talking ! So you're saying :"
        });
      }
      this.messages.push({
        author: "Me",
        content: userTextMessage
      });
      this.scrollMessage();

      this.increaseRequestsCounter();

      axios
        .get("/chatbot/get-response", {
          params: { userTextMessage }
        })
        .then(response => {
          if (response.data !== null) {
            const data = response.data;

            if (data.intent === "<EOC>") {
              this.messages.push({
                author: "chatbot",
                content: data.msg
              });
              this.showWasHelpful = true;
            } else {
              this.messages.push({
                author: "chatbot",
                content: data.msg
              });
            }
            this.decreaseRequestCounter();
            this.scrollMessage();
          }
        })
        .catch(err => {
          alert("Error, no internet connexion.");
          console.trace(err);
        });
    },
    scrollMessage() {
      this.$nextTick(function() {
        this.$refs.messageContainerScroll.scrollTop = this.$refs.messageContainerScroll.scrollHeight;
      });
    },

    increaseRequestsCounter() {
      this.requestsCounter++;
      this.isWaitingChatbotResponse = true;
    },
    decreaseRequestCounter() {
      this.requestsCounter--;
      if (this.requestsCounter === 0) {
        this.isWaitingChatbotResponse = false;
      }
    },
    sendHelpfulStatus(status) {
      const wasHelpful = status ? "yes" : "no";
      axios
        .get("/chatbot/end_chat", {
          params: { was_helpful: wasHelpful }
        })
        .then(response => {
          if (response.data !== null) {
            this.endConversation = true;
          }
        })
        .catch(err => {
          alert("Error, no internet connexion.");
          console.trace(err);
        });
    },
    startConversation() {
      this.initializeData();
      this.sendStartConversationRequest();
    },
    initializeData() {
      this.messages = [];
      this.endConversation = false;
      this.showWasHelpful = false;
    },
    sendStartConversationRequest() {
      this.scrollMessage();
      this.increaseRequestsCounter();
      axios
        .get("/chatbot/start_chat")
        .then(response => {
          if (response.data !== null && response.data.intent === "<SOC>") {
            const data = response.data;
            this.messages.push({
              author: "chatbot",
              content: data.msg
            });
            this.decreaseRequestCounter();
            this.scrollMessage();
          } else {
            alert("Wrong answer from the server");
          }
        })
        .catch(err => {
          alert("Error, no internet connexion.");
          console.trace(err);
        });
    }
  }
};
</script>

<style lang="scss">
@import "/static/materialize/css/materialize.css";
@import "/static/css/chatbot.css";
</style>
